import { createSlice, PayloadAction } from '@reduxjs/toolkit';



// redux-persist ko persist c cc gi tr non-serializable,
// nh class instance, Promise, Date
export interface Player {
  id: string,
  index: number,
  name: string,
}

export interface Round {
  index: number,
  score: number[],
  createOn: string
}

export interface Game {
  id: string,
  name: string,
  players: Player[],
  rounds: Round[],
  createOn: string
}

export const ghiDiemSlice = createSlice({
  name: 'ghiDiem',
  initialState: {
    games: [] as Game[]
  },
  reducers: {
    addGame(state, action: PayloadAction<Game>) {
      state.games.push(action.payload);
    },

    addNewRoundToGame(state, action: PayloadAction<{ gameId: string, round: Round }>) {
      let _game = state.games.find(g => g.id == action.payload.gameId)
      _game?.rounds.push(action.payload.round);
    }
  },
});

export const {
  addGame,
  addNewRoundToGame
} = ghiDiemSlice.actions;
export default ghiDiemSlice.reducer;




